// src/components/SingleRoomGrid.js
import React, { useState, useEffect, useRef } from 'react';
import { createPortal } from 'react-dom';
import googleSheetsService from '../services/googleSheetsService';
import icalService from '../services/icalService';
import { HORAIRES, SERVICES, OBJETS_RESERVATION, JOURS_FERIES, COULEURS_OBJETS, SALLES_ADMIN_ONLY, ADMINISTRATEURS } from '../config/googleSheets';
import { getSalleData, sallesData } from '../data/sallesData';
import SalleCard from './SalleCard';
import './SingleRoomGrid.css';

function SingleRoomGrid({ selectedRoom, editingReservation, onBack, onSuccess }) {
  // PROBLME 7 : Dimanche  semaine suivante
  useEffect(() => {
    const today = new Date();
    
    // Si dimanche ET premire ouverture de la salle
    if (today.getDay() === 0 && !hasManuallyNavigated.current) {
      // Passer au lundi suivant (demain)
      const tomorrow = new Date(today);
      tomorrow.setDate(today.getDate() + 1);
      tomorrow.setHours(0, 0, 0, 0);
      
      // Calculer le lundi de cette semaine-l
      const dayOfWeek = tomorrow.getDay();
      const mondayDate = new Date(tomorrow);
      mondayDate.setDate(tomorrow.getDate() - (dayOfWeek === 0 ? 6 : dayOfWeek - 1));
      
      setCurrentWeekStart(mondayDate);
    }
  }, [selectedRoom]);
  
  // Ref pour savoir si navigation manuelle
  const hasManuallyNavigated = React.useRef(false);

  const getMondayOfWeek = (d) => { const date = new Date(d); const day = date.getDay(); const diff = date.getDate() - day + (day === 0 ? -6 : 1); const monday = new Date(date.setDate(diff)); monday.setHours(0, 0, 0, 0); return monday; };
  const [currentWeekStart, setCurrentWeekStart] = useState(() => getMondayOfWeek(new Date()));
  const [reservations, setReservations] = useState([]);
  const [selections, setSelections] = useState([]);
  const [isDragging, setIsDragging] = useState(false);
  const [dragStart, setDragStart] = useState(null);
  const [mouseDownPos, setMouseDownPos] = useState(null);
  const [loading, setLoading] = useState(false);
  const [showForm, setShowForm] = useState(false);
  const [hoveredReservation, setHoveredReservation] = useState(null);
  const [isFading, setIsFading] = useState(false);
  const [popupPosition, setPopupPosition] = useState({ x: 0, y: 0 });

  // REFERENCE POUR LE SCROLL AUTO
  const sidebarRef = useRef(null);
  // grilleRef supprim

  const [blockedDayModal, setBlockedDayModal] = useState(false);
  const [adminPasswordModal, setAdminPasswordModal] = useState({ show: false, password: '' });
  const [isAdminUnlocked, setIsAdminUnlocked] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submissionProgress, setSubmissionProgress] = useState({ current: 0, total: 0 });
  const [successModal, setSuccessModal] = useState({ show: false, reservations: [], message: '' });
  const [warningModal, setWarningModal] = useState({ show: false, conflicts: [], validReservations: [] });
  const [formData, setFormData] = useState({ nom: '', prenom: '', email: '', telephone: '', service: '', objet: '', description: '', recurrence: false, recurrenceType: 'weekly', recurrenceJusquau: '', agencement: '', nbPersonnes: '' });
  const salleData = getSalleData(selectedRoom);
  const salleInfo = sallesData.find(s => s.nom === salleData?.nom);
  const dispositions = salleInfo?.dispositions || null;

  // --- CORRECTION : VRIFICATION ADMIN PLUS LARGE ---
  // On vrifie si le nom de la salle slectionne contient l'un des noms de la liste admin
  const isAdminOnlyRoom = (room) => {
    if (!room) return false;
    return SALLES_ADMIN_ONLY.some(adminRoom => room.includes(adminRoom) || adminRoom.includes(room));
  };

  useEffect(() => { loadWeekReservations(); }, [currentWeekStart, selectedRoom, editingReservation]);
  
  // useEffect scroll supprim (grilleRef n'existe plus)

  // --- SCROLL AUTO AU CHARGEMENT ---
  useEffect(() => {
    if (sidebarRef.current) {
        setTimeout(() => {
            // Scroll retir (Problme 6)
        }, 100);
    }
  }, [selectedRoom]);

  const loadWeekReservations = async () => { 
    setLoading(true); 
    try { 
      const allReservations = await googleSheetsService.getAllReservations(); 
      const weekEnd = new Date(currentWeekStart); 
      weekEnd.setDate(currentWeekStart.getDate() + 6); 
      
      const filtered = allReservations.filter(res => { 
        const resSalleName = res.salle.split(' - ')[0]; 
        if (resSalleName !== selectedRoom && res.salle !== selectedRoom) return false; 
        if (res.statut === 'cancelled') return false; 
        
        //  FILTRAGE LOCAL : Exclure la rservation en cours d'dition
        if (editingReservation && res.id === editingReservation.id) {
          console.log(' Rservation en dition filtre (non affiche comme occupe):', res.id);
          return false;
        }
        
        const resDate = new Date(res.dateDebut); 
        return resDate >= currentWeekStart && resDate <= weekEnd; 
      }); 
      
      setReservations(filtered); 
    } catch (error) { 
      console.error('Erreur chargement:', error); 
    } 
    setLoading(false); 
  };
  
  const handleAdminPasswordSubmit = () => { if (adminPasswordModal.password === 'R3sa@M0rep@s78') { setIsAdminUnlocked(true); setAdminPasswordModal({ show: false, password: '' }); } else { alert(' Mot de passe incorrect'); setAdminPasswordModal({ ...adminPasswordModal, password: '' }); } };
  const getDates = () => { const dates = []; for (let i = 0; i < 7; i++) { const date = new Date(currentWeekStart); date.setDate(currentWeekStart.getDate() + i); dates.push(date); } return dates; };
  const dates = getDates();
  const weekDays = ['Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche'];
  const timeSlots = []; for (let h = HORAIRES.HEURE_DEBUT; h < HORAIRES.HEURE_FIN; h += 0.5) { timeSlots.push(h); }
  const formatWeekRange = () => { const start = currentWeekStart; const end = new Date(currentWeekStart); end.setDate(currentWeekStart.getDate() + 6); return `${start.toLocaleDateString('fr-FR', { day: 'numeric', month: 'long' })} - ${end.toLocaleDateString('fr-FR', { day: 'numeric', month: 'long' })} ${start.getFullYear()}`; };
  const handlePreviousWeek = () => { const d = new Date(currentWeekStart); d.setDate(currentWeekStart.getDate() - 7); setCurrentWeekStart(d); };
  const handleNextWeek = () => { const d = new Date(currentWeekStart); d.setDate(currentWeekStart.getDate() + 7); setCurrentWeekStart(d); };
  const handlePreviousMonth = () => {
    hasManuallyNavigated.current = true; const d = new Date(currentWeekStart); d.setMonth(d.getMonth() - 1); setCurrentWeekStart(getMondayOfWeek(d)); };
  const handleNextMonth = () => {
    hasManuallyNavigated.current = true; const d = new Date(currentWeekStart); d.setMonth(d.getMonth() + 1); setCurrentWeekStart(getMondayOfWeek(d)); };
  const handleCurrentWeek = () => { setCurrentWeekStart(getMondayOfWeek(new Date())); };
  const isJourFerie = (date) => JOURS_FERIES.includes(googleSheetsService.formatDate(date));
  const isDimanche = (date) => date.getDay() === 0;
  const isDateInPast = (date) => { const t = new Date(); t.setHours(0,0,0,0); const c = new Date(date); c.setHours(0,0,0,0); return c < t; };
  const isSlotReserved = (dayIndex, slotStart) => { const slotEnd = slotStart + 0.5; const date = dates[dayIndex]; const dateStr = googleSheetsService.formatDate(date); return reservations.some(res => { if (res.dateDebut !== dateStr) return false; const resStart = googleSheetsService.timeToFloat(res.heureDebut); const resEnd = googleSheetsService.timeToFloat(res.heureFin); return (slotStart < resEnd && slotEnd > resStart); }); };
  const getReservation = (dayIndex, slotStart) => { const slotEnd = slotStart + 0.5; const date = dates[dayIndex]; const dateStr = googleSheetsService.formatDate(date); return reservations.find(res => { if (res.dateDebut !== dateStr) return false; const resStart = googleSheetsService.timeToFloat(res.heureDebut); const resEnd = googleSheetsService.timeToFloat(res.heureFin); return (slotStart < resEnd && slotEnd > resStart); }); };
  const isSlotSelected = (dayIndex, slot) => selections.some(sel => sel.dayIndex === dayIndex && sel.hour === slot);
  
  const handleMouseDown = (dayIndex, hour, date, event) => {
    // PROBLME 7 : Permettre popup sur semaines passes
    if (isDimanche(date) || isJourFerie(date)) {
      setBlockedDayModal(true);
      return;
    }
    
    // PRIORIT 1 : Si crneau rserv, afficher popup (AVANT vrification admin)
    const dateStr = googleSheetsService.formatDate(date);
    const reservation = reservations.find(r => 
      r.salle.includes(selectedRoom.split(' - ')[0]) && 
      r.dateDebut === dateStr &&
      hour >= googleSheetsService.timeToFloat(r.heureDebut) && 
      hour < googleSheetsService.timeToFloat(r.heureFin)
    );
    if (reservation) {
      // PROBLME 2 & 6 : Popup au pointeur (pas centre)
      const popupWidth = 320;
      const popupHeight = 250;
      
      let finalX, finalY, transform;
      
      if (window.innerWidth < 1280) {
        // RESPONSIVE : Centre horizontal SANS transform
        finalX = (window.innerWidth - popupWidth) / 2;
        finalY = Math.max(10, Math.min(window.innerHeight - popupHeight - 10, event.clientY - 50));
        transform = 'translate(0, -100%)';  // Seulement vertical
      } else {
        // DESKTOP : Centr EXACTEMENT sur pointeur
        const maxX = window.innerWidth - popupWidth - 10;
        const minX = 10;
        finalX = Math.max(minX, Math.min(maxX, event.clientX));
        finalY = event.clientY;  // Pas de dcalage
        transform = 'translate(-50%, -50%)';  // Centre popup sur pointeur
      }
      
      setHoveredReservation(reservation);
      setPopupPosition({ x: finalX, y: finalY, transform });
      return;
    }
    
    // PRIORIT 2 : Si salle verrouille ET crneau VIDE, demander mot de passe
    if (isAdminOnlyRoom(selectedRoom) && !isAdminUnlocked) {
      setAdminPasswordModal({ show: true, password: '' });
      return;
    }
    
    // PRIORIT 3 : Bloquer slection sur dates passes (BUG CRITIQUE)
    if (isDateInPast(date)) {
      return;  // Pas de slection sur dates passes
    }
    
    // PRIORIT 4 : Dmarrer slection
    setDragStart({ dayIndex, hour });
    setMouseDownPos({ dayIndex, hour, date });
  };
  const handleMouseEnter = (dayIndex, hour, date) => {
    if (!dragStart) return;
    
    // Dmarrer drag ds le premier mouvement
    let justStartedDragging = false;
    if (!isDragging && mouseDownPos) {
      if (dayIndex !== mouseDownPos.dayIndex || hour !== mouseDownPos.hour) {
        setIsDragging(true);
        justStartedDragging = true;
      } else {
        return;
      }
    }
    
    // CORRECTION : Ne pas return si on vient de dmarrer le drag
    if (!isDragging && !justStartedDragging) return;
    if (isSlotReserved(dayIndex, hour) || isDimanche(date) || isJourFerie(date) || isDateInPast(date)) return;
    
    // Construire slection rectangle
    const newSelections = [...selections];
    const minDay = Math.min(dragStart.dayIndex, dayIndex);
    const maxDay = Math.max(dragStart.dayIndex, dayIndex);
    const minHour = Math.min(dragStart.hour, hour);
    const maxHour = Math.max(dragStart.hour, hour);
    
    for (let d = minDay; d <= maxDay; d++) {
      const dayDate = dates[d];
      if (!isDimanche(dayDate) && !isJourFerie(dayDate) && !isDateInPast(dayDate)) {
        for (let h = minHour; h <= maxHour; h += 0.5) {
          const exists = newSelections.some(sel => sel.dayIndex === d && sel.hour === h);
          if (!exists && !isSlotReserved(d, h)) {
            newSelections.push({ dayIndex: d, hour: h, date: dates[d] });
          }
        }
      }
    }
    setSelections(newSelections);
  };
  
  const handleMouseUp = () => { 
    let shouldScroll = false;
    if (!isDragging && mouseDownPos) { 
      const { dayIndex, hour, date } = mouseDownPos; 
      const alreadySelected = selections.some(sel => sel.dayIndex === dayIndex && sel.hour === hour); 
      if (alreadySelected) { 
        const newSelections = selections.filter(sel => !(sel.dayIndex === dayIndex && sel.hour === hour)); 
        setSelections(newSelections); 
        if (newSelections.length === 0) setShowForm(false); 
      } else { 
        setSelections([...selections, { dayIndex, hour, date }]); 
        setShowForm(true);
        shouldScroll = true;
      } 
    } else if (isDragging && selections.length > 0) {
      setShowForm(true);
      shouldScroll = true;
    }
    
    if (shouldScroll && sidebarRef.current) {
        setTimeout(() => {
            // Scroll retir (Problme 6)
        }, 100);
    }

    setIsDragging(false); 
    setDragStart(null); 
    setMouseDownPos(null); 
  };

  const handleCancelSelection = () => { setSelections([]); setShowForm(false); setFormData({ nom: '', prenom: '', email: '', telephone: '', service: '', objet: '', description: '', recurrence: false, recurrenceType: 'weekly', recurrenceJusquau: '', agencement: '', nbPersonnes: '' }); };
  const removeSelection = (index) => {
    // Identifier la rservation complte (crneaux contigus)
    const toRemove = mergedForDisplay[index];
    
    // Trouver TOUS les crneaux entre startHour et endHour
    const selectionsToRemove = selections.filter(sel => 
      sel.date && toRemove.date &&
      sel.date.getTime() === toRemove.date.getTime() &&
      sel.hour >= toRemove.hour &&
      sel.hour < toRemove.endHour
    );
    
    // Retirer TOUS les crneaux d'un coup
    const newSelections = selections.filter(sel => 
      !selectionsToRemove.some(remove => 
        sel.date && remove.date &&
        sel.date.getTime() === remove.date.getTime() &&
        Math.abs(sel.hour - remove.hour) < 0.01
      )
    );
    
    setSelections(newSelections);
    if (newSelections.length === 0) setShowForm(false);
  };
    const preMergeSelections = (selections) => {
    if (selections.length === 0) return [];
    
    // Grouper par date
    const byDate = {};
    selections.forEach(sel => {
      const dateKey = sel.date instanceof Date ? sel.date.toISOString().split('T')[0] : sel.date;
      if (!byDate[dateKey]) byDate[dateKey] = [];
      byDate[dateKey].push(sel);
    });
    
    const merged = [];
    for (const dateKey in byDate) {
      const slots = byDate[dateKey].sort((a, b) => a.hour - b.hour);
      let i = 0;
      
      while (i < slots.length) {
        const current = {
          date: slots[i].date,
          dayIndex: slots[i].dayIndex,
          hour: slots[i].hour,
          endHour: slots[i].hour + 0.5
        };
        
        // Fusionner crneaux contigus du MME JOUR
        // CORRECTION : Utiliser slots[i+1].hour au lieu de .startHour
        while (i + 1 < slots.length && Math.abs(current.endHour - slots[i + 1].hour) < 0.01) {
          current.endHour = slots[i + 1].hour + 0.5;
          i++;
        }
        
        merged.push(current);
        i++;
      }
    }
    return merged;
  };
  const generateRecurrenceDates = (startDate, endDate, type) => { const dates = []; const current = new Date(startDate); const end = new Date(endDate); if (type === 'monthly') current.setMonth(current.getMonth() + 1); else if (type === 'biweekly') current.setDate(current.getDate() + 14); else current.setDate(current.getDate() + 7); while (current <= end) { dates.push(new Date(current)); if (type === 'monthly') current.setMonth(current.getMonth() + 1); else if (type === 'biweekly') current.setDate(current.getDate() + 14); else current.setDate(current.getDate() + 7); } return dates; };
  const checkConflicts = (candidates, allExistingReservations) => { const conflicts = []; const valid = []; candidates.forEach(candidate => { const candidateStart = new Date(`${candidate.dateDebut}T${candidate.heureDebut}`); const candidateEnd = new Date(`${candidate.dateFin}T${candidate.heureFin}`); const hasConflict = allExistingReservations.some(existing => { if (existing.statut === 'cancelled') return false; if (existing.salle !== candidate.salle && existing.salle.split(' - ')[0] !== candidate.salle) return false; const existingStart = new Date(`${existing.dateDebut}T${existing.heureDebut}`); const existingEnd = new Date(`${existing.dateFin || existing.dateDebut}T${existing.heureFin}`); return (candidateStart < existingEnd && candidateEnd > existingStart); }); if (hasConflict) conflicts.push(candidate); else valid.push(candidate); }); return { conflicts, valid }; };
  const finalizeReservation = async (reservationsToSave) => { setIsSubmitting(true); setSubmissionProgress({ current: 0, total: reservationsToSave.length }); setWarningModal({ show: false, conflicts: [], validReservations: [] }); try { const createdReservations = []; for (const res of reservationsToSave) { const result = await googleSheetsService.addReservation(res); createdReservations.push({ ...res, id: result.id }); setSubmissionProgress(prev => ({ ...prev, current: prev.current + 1 })); } setSuccessModal({ show: true, reservations: createdReservations, message: ' Rservation confirme !' }); setSelections([]); setShowForm(false); loadWeekReservations(); } catch (error) { alert('Erreur: ' + error.message); } finally { setIsSubmitting(false); } };
  const handleFormSubmit = async (e) => { e.preventDefault(); 
    
    // MODIFICATION : Supprimer ancienne rservation si mode dition
    if (editingReservation) {
      try {
        console.log('Mode dition : Suppression rservation', editingReservation.id);
        await googleSheetsService.deleteReservation(editingReservation.id);
      } catch (err) {
        console.error('Erreur suppression:', err);
        alert('Erreur lors de la modification');
        return;
      }
    }
    
    if (dispositions && !formData.agencement) return alert(' Veuillez choisir une disposition.'); setIsSubmitting(true); try { const mergedSelections = preMergeSelections(selections); let allCandidates = []; mergedSelections.forEach(sel => { const dateStr = googleSheetsService.formatDate(sel.date); const baseRes = { salle: selectedRoom, service: formData.service, nom: formData.nom, prenom: formData.prenom, email: formData.email, telephone: formData.telephone, dateDebut: dateStr, dateFin: dateStr, heureDebut: googleSheetsService.formatTime(sel.hour), heureFin: googleSheetsService.formatTime(sel.endHour), objet: formData.objet, description: formData.description, recurrence: formData.recurrence ? 'OUI' : 'NON', recurrenceJusquau: formData.recurrenceJusquau, agencement: formData.agencement || '', nbPersonnes: formData.nbPersonnes, statut: 'active' }; allCandidates.push(baseRes); if (formData.recurrence && formData.recurrenceJusquau) { const selDateObj = sel.date instanceof Date ? sel.date : new Date(sel.date); const dates = generateRecurrenceDates(selDateObj, new Date(formData.recurrenceJusquau), formData.recurrenceType); dates.forEach(date => { const dateRecurStr = googleSheetsService.formatDate(date); allCandidates.push({ ...baseRes, dateDebut: dateRecurStr, dateFin: dateRecurStr }); }); } }); const allExisting = await googleSheetsService.getAllReservations(); const { conflicts, valid } = checkConflicts(allCandidates, allExisting); setIsSubmitting(false); if (conflicts.length > 0) { setWarningModal({ show: true, conflicts, validReservations: valid }); } else { await finalizeReservation(valid); } } catch (error) { alert('Erreur: ' + error.message); setIsSubmitting(false); } };
  const mergedForDisplay = selections.length > 0 ? preMergeSelections(selections) : [];
  const successModalContent = successModal.show ? ( <div className="success-modal-overlay" onClick={() => setSuccessModal({ ...successModal, show: false })} style={{ position: 'fixed', top: 0, left: 0, width: '100%', height: '100%', background: 'rgba(0,0,0,0.6)', zIndex: 99999, display: 'flex', alignItems: 'center', justifyContent: 'center', paddingBottom: '70px' }}> <div className="success-modal" onClick={e => e.stopPropagation()}> <div className="success-modal-header"><h2>{successModal.reservations.length > 1 ? "Rservations confirmes !" : "Rservation confirme !"}</h2></div> <div className="success-modal-body"> <p className="success-subtitle"><b>{successModal.reservations.length} {successModal.reservations.length > 1 ? "crneaux confirms" : "crneau confirm"}</b></p> <div className="reservations-list"> {successModal.reservations.map((res, i) => ( <div key={i} className="reservation-item-success"> <span className="calendar-icon"></span> {res.salle.split(' - ')[0]} - {new Date(res.dateDebut).toLocaleDateString('fr-FR')} : {res.heureDebut} - {res.heureFin} </div> ))} </div> <div className="ical-download-section"> <button className="download-ical-button" onClick={() => icalService.generateAndDownload(successModal.reservations)}> Tlcharger .ics</button> </div> </div> <div className="success-modal-footer"><button className="close-modal-button" onClick={() => setSuccessModal({ ...successModal, show: false })}>Fermer</button></div> </div> </div> ) : null;

  // --- GESTION DU TIMER (4 SECONDES) ---
  useEffect(() => {
    let fadeTimer;
    let removeTimer;

    if (hoveredReservation) {
      setIsFading(false); // Reset

      fadeTimer = setTimeout(() => {
        setIsFading(true); // Fade out start
        removeTimer = setTimeout(() => {
          setHoveredReservation(null);
          setIsFading(false);
        }, 400); // Wait for transition
      }, 4000); // 4 sec delay
    }

    return () => {
      clearTimeout(fadeTimer);
      clearTimeout(removeTimer);
    };
  }, [hoveredReservation]);

  // MODIFICATION : Pr-remplir formulaire si editingReservation

  // CORRECTION MAJEURE : Pr-remplir formulaire + pr-slectionner crneaux + naviguer vers bonne semaine
  useEffect(() => {
    if (editingReservation) {
      console.log(' Mode dition activ pour rservation:', editingReservation.id);
      
      // 1. Pr-remplir le formulaire avec les donnes existantes
      setFormData({
        nom: editingReservation.nom || '',
        prenom: editingReservation.prenom || '',
        email: editingReservation.email || '',
        telephone: editingReservation.telephone || '',
        service: editingReservation.service || '',
        objet: editingReservation.objet || '',
        description: editingReservation.description || '',
        recurrence: editingReservation.recurrence || false,
        recurrenceType: editingReservation.recurrenceType || 'weekly',
        recurrenceJusquau: editingReservation.recurrenceJusquau || '',
        agencement: editingReservation.agencement || '',
        nbPersonnes: editingReservation.nbPersonnes || ''
      });
      
      // 2. Calculer la semaine de la rservation et naviguer vers elle
      const resDate = new Date(editingReservation.dateDebut);
      const resWeekStart = getMondayOfWeek(resDate);
      setCurrentWeekStart(resWeekStart);
      hasManuallyNavigated.current = true; // Empcher le reset automatique dimanche
      
      // 3. Pr-slectionner les crneaux de la rservation existante
      const dayOfWeek = resDate.getDay();
      // Conversion : 0=Dimanche6, 1=Lundi0, 2=Mardi1, etc.
      const dayIndex = dayOfWeek === 0 ? 6 : dayOfWeek - 1;
      
      const startHour = googleSheetsService.timeToFloat(editingReservation.heureDebut);
      const endHour = googleSheetsService.timeToFloat(editingReservation.heureFin);
      
      const newSelections = [];
      for (let h = startHour; h < endHour; h += 0.5) {
        newSelections.push({
          dayIndex: dayIndex,
          hour: h,
          date: resDate
        });
      }
      
      setSelections(newSelections);
      setShowForm(true);
      
      console.log(' Modification prte:', {
        semaine: resWeekStart.toLocaleDateString('fr-FR'),
        jour: dayIndex,
        crneaux: newSelections.length,
        horaire: `${editingReservation.heureDebut} - ${editingReservation.heureFin}`
      });
    }
  }, [editingReservation]);


  return (
    <>
      {successModal.show && createPortal(successModalContent, document.body)}
      <div className="single-room-container">
        <div className="week-navigation">
          <div className="nav-group-left">
            <button onClick={onBack} className="back-button-inline"> Retour</button>
            <h2 className="room-title-inline"> {salleData?.nom || selectedRoom}</h2>
          </div>
          <div className="nav-group-center">
            <button className="week-nav-btn" onClick={handlePreviousMonth}></button>
            <button className="week-nav-btn" onClick={handlePreviousWeek}></button>
            <button className="week-nav-btn" style={{padding: '0.6rem 1.2rem', fontSize: '0.9rem'}} onClick={handleCurrentWeek}>Cette semaine</button>
            <h3 className="week-date-display">{formatWeekRange()}</h3>
            <button className="week-nav-btn" onClick={handleNextWeek}></button>
            <button className="week-nav-btn" onClick={handleNextMonth}></button>
          </div>
          <div className="nav-group-right-spacer"></div>
        </div>

        <div className="single-room-layout">
          {/* CIBLE DU SCROLL VIA LA REF */}
          <div className="room-sidebar" ref={sidebarRef}>
            {!showForm && (
              <>
                <SalleCard salle={selectedRoom} />
                {/* INSTRUCTION BUREAU (Cach sur Mobile via CSS) */}
                <div className="no-selection-message desktop-legend"><p>Slectionnez un ou plusieurs crneaux pour commencer votre rservation </p></div>
              </>
            )}
            {showForm && selections.length > 0 && (
              <div className="room-form-container">
                <h3 className="form-title">{selections.length > 1 ? `Rservation de ${selections.length} crneaux` : 'Confirmer la rservation'}</h3>
                <div className="selections-summary">
                  {mergedForDisplay.map((sel, idx) => (
                    <div key={idx} className="selection-item">{googleSheetsService.formatDate(sel.date)} : {googleSheetsService.formatTime(sel.hour)} - {googleSheetsService.formatTime(sel.endHour)}<button className="remove-selection-btn" onClick={() => removeSelection(idx)}></button></div>
                  ))}
                </div>
                <form onSubmit={handleFormSubmit} className="room-form">
                  <div className="form-row"><input className="form-input" placeholder="Nom *" value={formData.nom} onChange={e => setFormData({...formData, nom: e.target.value})} required style={{flex:1}} /><input className="form-input" placeholder="Prnom" value={formData.prenom} onChange={e => setFormData({...formData, prenom: e.target.value})} style={{flex:1}} /></div>
                  <input className="form-input" placeholder="Email *" value={formData.email} onChange={e => setFormData({...formData, email: e.target.value})} required />
                  <input className="form-input" placeholder="Tlphone" value={formData.telephone} onChange={e => setFormData({...formData, telephone: e.target.value})} />
                  <select className="form-select" value={formData.service} onChange={e => setFormData({...formData, service: e.target.value})} required><option value="">Choisissez le service...</option>{SERVICES.map(s => <option key={s} value={s}>{s}</option>)}</select>
                  <select className="form-select" value={formData.objet} onChange={e => setFormData({...formData, objet: e.target.value})} required><option value="">Choisissez l'objet...</option>{OBJETS_RESERVATION.map(o => <option key={o} value={o}>{o}</option>)}</select>
                  {dispositions && (
                    <>
                      {/* VALIDATION: Ajout de 'required' */}
                      <select className="form-select disposition-select" value={formData.agencement} onChange={e => setFormData({...formData, agencement: e.target.value})} required><option value="">Disposition souhaite *</option>{dispositions.map(d => <option key={d} value={d}>{d}</option>)}</select>
                      {/* VALIDATION: Ajout de 'required', 'min' et 'max' dynamique */}
                      {(selectedRoom.includes('Conseil') || selectedRoom.includes('Mariages')) && <input type="number" className="form-input" placeholder={`Nombre de personnes prvues (max ${selectedRoom.includes('Mariages') ? 30 : 100}) *`} value={formData.nbPersonnes} onChange={e => setFormData({...formData, nbPersonnes: e.target.value})} required min="1" max={selectedRoom.includes('Mariages') ? 30 : 100} />}
                    </>
                  )}
                  <textarea className="form-textarea" placeholder="Description (facultative)" rows="2" value={formData.description} onChange={e => setFormData({...formData, description: e.target.value})} />
                  <div className="recurrence-section-styled"><div className="recurrence-box"><input type="checkbox" checked={formData.recurrence} onChange={e => setFormData({...formData, recurrence: e.target.checked})} /><label>Rservation rcurrente</label></div>
                  {formData.recurrence && (<div className="recurrence-options slide-down"><div className="form-group"><select className="form-select" value={formData.recurrenceType} onChange={e => setFormData({...formData, recurrenceType: e.target.value})}><option value="weekly">Chaque semaine</option><option value="biweekly">Une semaine sur 2</option><option value="monthly">Chaque mois</option></select></div><div className="form-group" style={{marginBottom:0}}><input type="date" className="form-input" placeholder="JJ/MM/AAAA" value={formData.recurrenceJusquau} onChange={e => setFormData({...formData, recurrenceJusquau: e.target.value})} min={googleSheetsService.formatDate(new Date())} required={formData.recurrence} /></div></div>)}</div>
                  <div className="form-actions"><button type="button" className="btn-cancel" onClick={handleCancelSelection}>Annuler</button><button type="submit" className="btn-submit" disabled={isSubmitting}>Valider</button></div>
                </form>
              </div>
            )}
          </div>
          <div className="week-grid-container">
            <table  className="week-grid" onMouseLeave={() => { /* Rien ici */ }} onMouseUp={handleMouseUp}>
              <thead>
                <tr>
                  <th className="hour-header">Heure</th>
                  {dates.map((date, idx) => (
                    <th key={idx} className="day-header">
                      <div className="day-name">
                        <span className="name-full">{weekDays[idx]}</span>
                        <span className="name-short">{weekDays[idx].slice(0, 3)}</span>
                      </div>
                      <div className="day-date">{date.getDate()}/{date.getMonth() + 1}</div>
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody>{timeSlots.map(slot => (
                <tr key={slot}>
                  <td className={slot % 1 === 0 ? 'hour-cell-full' : 'hour-cell-half'}>
                    {slot % 1 === 0 ? `${slot}h` : ''}
                  </td>
                  {dates.map((date, dayIndex) => { 
                    const reserved = isSlotReserved(dayIndex, slot); 
                    const selected = isSlotSelected(dayIndex, slot); 
                    const blocked = isDimanche(date) || isJourFerie(date); 
                    const past = isDateInPast(date); 
                    const reservation = getReservation(dayIndex, slot); 
                    const isFullHour = slot % 1 === 0;
                    
                    // --- VRIFICATION ADMIN SCURISE ---
                    const isAdmin = isAdminOnlyRoom(selectedRoom);
                    
                    let bgStyle = {}; 
                    if (reserved && reservation) bgStyle.backgroundColor = COULEURS_OBJETS[reservation.objet] || '#ccc'; 
                    
                    let cellClass = `time-slot`;
                    cellClass += isFullHour ? ' full-hour-border' : ' half-hour-border';
                    
                    if (reserved) cellClass += ' occupied';
                    if (selected) cellClass += ' selected';
                    // Blocked/Past : SEULEMENT si pas rserv
                    if (blocked && !reserved) cellClass += ' blocked';
                    if (past && !reserved) cellClass += ' past-date';
                    
                    // --- APPLIQUER LA CLASSE DE VERROUILLAGE MME LE MIDI ---
                    if (isAdmin && !isAdminUnlocked && !reserved) {
                        cellClass += ' admin-only-locked';
                    }
                    
                    if (slot >= 12 && slot < 14) cellClass += ' lunch-break';

                    return (
                      <td 
                        key={`${dayIndex}-${slot}`} 
                        className={cellClass} 
                        style={bgStyle} 
                        onMouseDown={(e) => handleMouseDown(dayIndex, slot, date, e)} 
                        onMouseEnter={() => handleMouseEnter(dayIndex, slot, date)} 
                      >
                      </td>
                    ); 
                  })}
                </tr>
              ))}</tbody>
            </table>
          </div>
        </div>
        
        {hoveredReservation && (
          <div className={`reservation-popup-card ${isFading ? 'fading-out' : ''}`}
          onClick={() => { setHoveredReservation(null); setIsFading(false); }}
          style={{ pointerEvents: 'auto', cursor: 'pointer', position: 'fixed', left: popupPosition.x, top: popupPosition.y, transform: popupPosition.transform || 'translate(-50%, -100%)', zIndex: 10001 }}>
            <div className="popup-card-header"><span className="popup-icon"></span><span className="popup-name">{hoveredReservation.prenom} {hoveredReservation.nom}</span></div>
            <div className="popup-card-body">
              {hoveredReservation.service && (
                <div className="popup-info-line">
                  <span className="popup-info-icon"></span>
                  <span className="popup-info-text">{hoveredReservation.service}</span>
                </div>
              )}
              {hoveredReservation.email && (
                <div className="popup-info-line">
                  <span className="popup-info-icon"></span>
                  <span className="popup-info-text">{hoveredReservation.email}</span>
                </div>
              )}
              {hoveredReservation.objet && (
                <div className="popup-info-line">
                  <span className="popup-info-icon"></span>
                  <span className="popup-info-text">{hoveredReservation.objet}</span>
                </div>
              )}
              <div className="popup-info-line">
                <span className="popup-info-icon"></span>
                <span className="popup-info-text">
                  {new Date(hoveredReservation.dateDebut).toLocaleDateString('fr-FR', { 
                    weekday: 'long', day: 'numeric', month: 'long' 
                  })}  {hoveredReservation.heureDebut} - {hoveredReservation.heureFin}
                </span>
              </div>
              {(hoveredReservation.salle.includes('Conseil') || 
                hoveredReservation.salle.includes('Mariages')) && (
                hoveredReservation.agencement && hoveredReservation.nbPersonnes && (
                  <div className="popup-info-line">
                    <span className="popup-info-icon"></span>
                    <span className="popup-info-text">
                      {hoveredReservation.agencement} | {hoveredReservation.nbPersonnes} pers.
                    </span>
                  </div>
                )
              )}
            </div>
          </div>
        )}
        
        
        {/*  CORRECTION : Modale harmonise avec classes CSS (responsive) */}
        {blockedDayModal && (
          <div className="blocked-modal-overlay" onClick={() => setBlockedDayModal(false)}>
            <div className="blocked-modal" onClick={(e) => e.stopPropagation()}>
              <div className="warning-modal-header">
                <span className="blocked-modal-emoji"></span>
                <h2 className="blocked-modal-title">Ferm</h2>
              </div>
              <p className="blocked-modal-message">
                Dimanche/Fri ferm.
              </p>
              <button onClick={() => setBlockedDayModal(false)} className="blocked-close-button">
                Fermer
              </button>
            </div>
          </div>
        )}
        {adminPasswordModal.show && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h3> Accs Administrateur</h3>
            <p>Cette salle est rserve. Veuillez saisir le mot de passe :</p>
            <input 
              type="password" 
              value={adminPasswordModal.password} 
              onChange={e => setAdminPasswordModal({...adminPasswordModal, password:e.target.value})} 
              onKeyPress={e => { if (e.key === 'Enter') handleAdminPasswordSubmit(); }}
              className="form-input"
              autoFocus
            />
            <div className="form-actions">
              <button className="btn-cancel" onClick={() => setAdminPasswordModal({show:false, password:''})}>Annuler</button>
              <button className="btn-submit" onClick={handleAdminPasswordSubmit}>Dbloquer</button>
            </div>
          </div>
        </div>
      )}
        {isSubmitting && <div className="modal-overlay"><div className="modal-content"><h3>Enregistrement...</h3><div className="progress-bar-container" style={{width:'100%', height:'10px', background:'#eee', borderRadius:'5px', margin:'1rem 0', overflow:'hidden'}}><div style={{width: `${(submissionProgress.current / submissionProgress.total) * 100}%`, height:'100%', background:'#4caf50', transition:'width 0.3s'}}></div></div><p>{submissionProgress.current} / {submissionProgress.total} crneaux traits</p></div></div>}
        {warningModal.show && <div className="modal-overlay"><div className="warning-modal"><div className="warning-modal-header"><h2> Attention !</h2></div><div className="warning-modal-body"><p>{warningModal.conflicts.length > 1 ? "Les dates suivantes..." : "La date suivante..."}</p><ul className="conflict-list">{warningModal.conflicts.map((res, i) => (<li key={i}>{new Date(res.dateDebut).toLocaleDateString('fr-FR')} : {res.heureDebut} - {res.heureFin}</li>))}</ul><p>Voulez-vous quand mme poursuivre ?</p></div><div className="warning-modal-footer"><button className="cancel-button" onClick={() => setWarningModal({ show: false, conflicts: [], validReservations: [] })}>Non, annuler</button><button className="submit-button" onClick={() => finalizeReservation(warningModal.validReservations)}>Oui, poursuivre</button></div></div></div>}
      </div>
    </>
  );
}

export default SingleRoomGrid;
