// Fonction helper pour obtenir les donnes d'une salle
export const getSalleData = (salleNom) => {
  // Si on reoit le nom complet (ex: "Salle Conseil - 100 Personnes")
  if (SALLES_DATA[salleNom]) {
    return SALLES_DATA[salleNom];
  }
  
  // Si on reoit le nom court (ex: "Salle Conseil"), chercher par nom
  const found = Object.entries(SALLES_DATA).find(([key, value]) => 
    value.nom === salleNom
  );
  
  return found ? found[1] : null;
};
