// src/App.js
// CORRECTION CRITIQUE : Gestion complte du mode dition avec routing vers SingleRoomGrid
import React, { useState, useEffect } from 'react';
import './App.css';
import CalendarView from './components/CalendarView';
import ReservationGrid from './components/ReservationGrid';
import SingleRoomGrid from './components/SingleRoomGrid';
import MyReservations from './components/MyReservations';
import AdminPanel from './components/AdminPanel';
import googleSheetsService from './services/googleSheetsService';
import emailService from './services/emailService';

function App() {
  const [currentView, setCurrentView] = useState('calendar');
  const [calendarTab, setCalendarTab] = useState('date');
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedRoom, setSelectedRoom] = useState(null);
  const [editReservationId, setEditReservationId] = useState(null);
  const [editingReservation, setEditingReservation] = useState(null); //  AJOUT
  const [userEmail, setUserEmail] = useState(localStorage.getItem('userEmail') || '');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const init = async () => {
      try {
        await googleSheetsService.initialize();
        emailService.init();
        setLoading(false);
      } catch (error) {
        console.error('Erreur d\'initialisation:', error);
        setLoading(false);
      }
    };
    init();
  }, []);

  // CORRECTION MAJEURE : Gestion complte du hash pour le mode dition
  useEffect(() => {
    const handleHashChange = async () => {
      const hash = window.location.hash;
      
      // Vrifier si on a un hash de modification (#calendar?...)
      if (hash.includes('?') && hash.includes('edit=')) {
        const params = new URLSearchParams(hash.split('?')[1]);
        const editId = params.get('edit');
        const salleParam = params.get('salle');
        const dateParam = params.get('date');
        
        if (editId) {
          console.log(' Mode dition dtect:', { editId, salleParam, dateParam });
          
          // PRIORIT 1 : Si on a une salle  Router vers SingleRoomGrid
          if (salleParam) {
            try {
              // Charger la rservation complte depuis Sheets
              const allReservations = await googleSheetsService.getAllReservations();
              const reservationToEdit = allReservations.find(r => r.id === editId);
              
              if (reservationToEdit) {
                console.log(' Rservation trouve:', reservationToEdit);
                
                const decodedSalle = decodeURIComponent(salleParam);
                setSelectedRoom(decodedSalle);
                setEditReservationId(editId);
                setEditingReservation(reservationToEdit); //  STOCKER la rservation complte
                setCurrentView('roomview');  //  SingleRoomGrid
                setCalendarTab('room');      //  Mode salle
                
                // Nettoyer le hash aprs 500ms
                setTimeout(() => {
                  window.history.replaceState(null, '', window.location.pathname);
                }, 500);
              } else {
                console.error(' Rservation non trouve:', editId);
                alert('Erreur : Rservation introuvable');
              }
            } catch (error) {
              console.error(' Erreur chargement rservation:', error);
              alert('Erreur lors du chargement de la rservation');
            }
          } 
          // FALLBACK : Si pas de salle mais une date  ReservationGrid
          else if (dateParam) {
            console.log(' Fallback ReservationGrid (pas de salle spcifie)');
            const date = new Date(dateParam);
            setSelectedDate(date);
            setEditReservationId(editId);
            setCurrentView('reservation');
            setCalendarTab('date');
            
            setTimeout(() => {
              window.history.replaceState(null, '', window.location.pathname);
            }, 500);
          }
        }
      }
    };
    
    window.addEventListener('hashchange', handleHashChange);
    handleHashChange(); // Appel immdiat au montage
    
    return () => window.removeEventListener('hashchange', handleHashChange);
  }, []);

  const isDateInPast = (date) => {
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    const checkDate = new Date(date);
    checkDate.setHours(0, 0, 0, 0);
    return checkDate < today;
  };

  const handleDateSelect = (date) => {
    if (isDateInPast(date)) {
      alert(' Impossible de rserver une date passe !\n\nVeuillez slectionner une date  partir d\'aujourd\'hui.');
      return;
    }
    setSelectedDate(date);
    setEditReservationId(null);
    setCurrentView('reservation');
    setCalendarTab('date');
  };

  const handleRoomSelect = (room, editingReservation = null) => {
    setSelectedRoom(room);
    setEditingReservation(editingReservation); //  Stocker si fourni
    setCurrentView('roomview');
    setCalendarTab('room');
  };

  const handleBackToCalendar = () => {
    setCurrentView('calendar');
    setSelectedDate(null);
    setSelectedRoom(null);
    setEditReservationId(null);
    setEditingReservation(null); //  Nettoyer
  };

  const handleBackFromRoom = () => {
    setCalendarTab('room');
    setCurrentView('calendar');
    setSelectedRoom(null);
    setEditReservationId(null);
    setEditingReservation(null); //  Nettoyer
  };

  const handleReservationSuccess = () => {
    setCurrentView('calendar');
    setEditReservationId(null);
    setEditingReservation(null); //  Nettoyer
  };

  const handleEditReservation = (reservation) => {
    const date = new Date(reservation.dateDebut);
    setSelectedDate(date);
    setEditReservationId(reservation.id);
    setCurrentView('reservation');
    setCalendarTab('date');
  };

  if (loading) {
    return <div className="app-loading"><div className="spinner"></div><p>Chargement de l'application...</p></div>;
  }

  return (
    <div className="App">
      <header className="app-header">
        <div className="header-content">
          <div className="header-title">
            <img 
              src={`${process.env.PUBLIC_URL}/images/Blason_ville_MAUREPAS.png`} 
              alt="Blason de Maurepas" 
              className="blason-maurepas"
            />
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start', justifyContent: 'center', textAlign: 'left', color: 'white' }}>
              <h1 style={{ margin: 0, lineHeight: '1.2', fontSize: '1.5rem', color: 'white' }}>Rservation de Salles</h1>
              <div style={{ fontSize: '1rem', fontWeight: '500', color: 'white' }}>Mairie de MAUREPAS</div>
            </div>
          </div>
          <nav className="main-nav">
            <button 
              className={currentView === 'calendar' ? 'active' : ''}
              onClick={() => setCurrentView('calendar')}
            >
               Calendrier
            </button>
            <button 
              className={currentView === 'myreservations' ? 'active' : ''}
              onClick={() => setCurrentView('myreservations')}
            >
               Mes Rservations
            </button>
            <button 
              className={currentView === 'admin' ? 'active' : ''}
              onClick={() => setCurrentView('admin')}
            >
               Admin
            </button>
          </nav>
        </div>
      </header>

      <main className="app-main">
        {currentView === 'calendar' && (
          <CalendarView 
            onDateSelect={handleDateSelect}
            onRoomSelect={handleRoomSelect}
            isDateInPast={isDateInPast}
            defaultView={calendarTab}
          />
        )}

        {currentView === 'reservation' && selectedDate && (
          <ReservationGrid 
            selectedDate={selectedDate}
            editReservationId={editReservationId}
            onBack={handleBackToCalendar}
            onSuccess={handleReservationSuccess}
          />
        )}

        {currentView === 'roomview' && selectedRoom && (
          <SingleRoomGrid 
            selectedRoom={selectedRoom}
            editingReservation={editingReservation}
            onBack={handleBackFromRoom}
            onSuccess={handleReservationSuccess}
          />
        )}

        {currentView === 'myreservations' && (
          <MyReservations 
            userEmail={userEmail} 
            setUserEmail={setUserEmail}
            onEditReservation={handleEditReservation}
          />
        )}

        {currentView === 'admin' && <AdminPanel />}
      </main>

      <footer className="app-footer">
        <p> 2026 Mairie de MAUREPAS - Systme de rservation de salles | DSI</p>
      </footer>
    </div>
  );
}

export default App;
